import React from 'react';
import {updatePopulation} from '../../actions/populationAction';

export default class Job extends React.Component {

    hire = () => {
        updatePopulation(this.props.job)
    }

    render() {
        return (
            <div>
                <button onClick={this.hire} className='btn btn-success JobButton'>
                    {"Hire " + this.props.job}
                </button>
            </div>
        );
    }
}


